.home-page{
    gap: 25px;
    margin-bottom: 60px;
}

.home-img{
    border-radius: 0;
    width: 65px;
}
.logo{ 
        height: 70px;
        margin-inline-end: 20px;
    }

    .logo-txt{
        font-size:2.5rem;
        font-weight:600;
    }

    .user-img{
        box-shadow: 8px 9px 18px -7px #000000;
        margin-inline-end: 40px;
        &.preview{
            margin: 3px 0 0 0;
        }
    }

    .user-name{
        font-size: 27px;
        font-weight: 700;
        color: #0f0144;
        margin-bottom: 10px;

        &.preview{ 
                font-size: 1.17em;}
    }

    .user-data{
        display: flex;
        align-items: center;
        h3{
            margin-inline-start: 10px;
            background-color: rgb(218 225 208 / 41%);
            border-radius: 10px;
            padding: 2px 1rem;
            font-weight: 600;
            box-shadow: 8px 9px 18px -7px #00000030;
         &.btc{
              margin-inline-start: 28px;
              }
        }
    }

    .main-img{
        margin-inline-start: 120px;
    }